nav{
    position: fixed;
    display: flex;
    text-align: center;
    box-shadow: 0 2px 2px 0 rgb(0 0 0 / 10%);
    justify-content: center;
    background-color: #69cca8;
    height: 40px;
    align-items: center;
    height: 60px;
    
    width: 100%;
    top: 0;
    left: 0;

}
 nav > ul{
 list-style-type: none;
 margin: 0px;
 padding: 0px;
 overflow: hidden;
}
 nav > ul > li{
    display: block;
    float: left;
    padding: 1px;
    margin: 12px;
  
}

nav > ul > li > a:hover{
    transition: .22s;
    color: rgb(47, 255, 189);
    text-shadow: 2px 3px 9px #7cff3f;
}

nav > ul > li > a{
    text-decoration: none;
    color: #97edd0
}
ul h1{
    display: inline;
    font-size: -webkit-xxx-large;
    font-family: 'Inconsolata', monospace;

}
.saylani:hover{
    transition: .22s;
    text-shadow:  2px -3px 5px #7cff3f;;
}